'use strict';

import mongoose from 'mongoose';
import {registerEvents} from './booktable.events';
import {Schema} from 'mongoose';
var BooktableSchema = new mongoose.Schema({
user:{
	type:Schema.ObjectId,
	ref:'User'
},
tableNumber:{
	type:Number
},
time:{
	type:String
},
date:{
	type:Date
},

userNotification:[{

}],
person:{
	type:Number
},
status:{
	type:String,
	default:'Pending'
},
createdAt:{
	type:Date,
	default:Date.now
}
});

registerEvents(BooktableSchema);
export default mongoose.model('Booktable', BooktableSchema);
